import React from 'react';
import ReactDOM from 'react-dom';
import App from './home/App';
import OnlineEdu from './projects/OnlineEdu';
import Pathfindr from './projects/Pathfindr';
import RemoteCode from './projects/RemoteCode';
import Webbuilder from './projects/Webbuilder';
import * as serviceWorker from './serviceWorker';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

ReactDOM.render(
	<React.StrictMode>
		<BrowserRouter>
			<Switch>
				<Route path="/online_edu" component={OnlineEdu} />
				<Route path="/pathfindr" component={Pathfindr} />
				<Route path="/remotecode" component={RemoteCode} />
				<Route path="/webbuilder" component={Webbuilder} />
				<Route path="/" component={App} />
			</Switch>
		</BrowserRouter>
	</React.StrictMode>,
	document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
