import React, { Component } from 'react';
import './style/IconList.scss';

export class IconList extends Component {
    render() {
        return (
            <ul className={this.props.layout}>
                {this.props.children}
            </ul>
        );
    };
}

class IconListItem extends Component {
    render() {
        return (
            <li>
                {this.props.children}
            </li>
        );
    }
}

class ClickableIconListItem extends Component {
    render() {
        return (
            <IconListItem>
                <a href={this.props.href}>
                    {this.props.children}
                </a>
            </IconListItem>
        );
    }
}

export default IconList
export { IconListItem, ClickableIconListItem }